import React, { useState, useContext, useMemo, useEffect } from "react";
import styled from "styled-components";
import List from "../List";
import IconFont from "../IconFont";
import { ColorContext } from "../Context";
import { weekGroup } from "../../utils/util";

const Wrapper = styled.div`
  width: 100%;

  .header {
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 15px;

    .left {
      display: flex;
      justify-content: flex-start;
      align-items: center;

      i {
        font-size: 26px;
        margin-right: 15px;
      }

      span {
        font-size: 18px;
      }
    }
    .right {
      width: calc(100% - 160px);
      display: flex;
      justify-content: space-around;
      height: 80%;
      padding: 0 30px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);

      .list {
        width: calc(80% / 7);
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: solid 1px transparent;
        margin-bottom: -1px;
        position: relative;
        cursor: pointer;

        &.active {
          border-bottom: solid 1px ${props => props.color};
          color: ${props => props.color};

          &:after {
            content: "";
            position: absolute;
            bottom: 0;
            width: 0;
            height: 0;
            border: solid 5px transparent;
            border-bottom: solid 5px ${props => props.color};
          }
        }
      }
    }
  }

  .content {
    margin-top: 15px;

    .section {
      display: none;

      &.active {
        display: block;
      }
    }
  }
`;

export const IndexNew = props => {
  const {
    value: { info = {}, list = [] }
  } = props;

  const [key, setKey] = useState(new Date().getDay());
  const value = useContext(ColorContext);
  const color = useMemo(() => value, [value]);

  const weekDay = ["day0", "day1", "day2", "day3", "day4", "day5", "day6"];
  const dayTitle = {
    day0: "周日",
    day1: "周一",
    day2: "周二",
    day3: "周三",
    day4: "周四",
    day5: "周五",
    day6: "周六"
  };
  const showList = weekGroup(list);
  return (
    <Wrapper color={color}>
      <div className="header">
        <div className="left">
          <IconFont type={info.icon} />
          <span>{info.name}</span>
        </div>
        <div className="right">
          {weekDay.map((item, index) => (
            <div
              className={index === key ? "list active" : "list"}
              onClick={() => setKey(index)}
              key={index}
            >
              <span>{dayTitle[item]}</span>
            </div>
          ))}
        </div>
      </div>
      <div className="content">
        {weekDay.map((item, index) => (
          <div
            className={index === key ? "section active" : "section"}
            key={index}
          >
            <List list={showList[item]} type={info.type} update={true} />
          </div>
        ))}
      </div>
    </Wrapper>
  );
};
