import React, { useContext, useMemo } from "react";
import styled from "styled-components";
import Router from "next/router";
import { fixedZero, inThisWeek } from "../../utils/util";
import { ColorContext } from "../Context";
import IconFont from "../IconFont";
import moment from "moment";
import { Empty } from "antd";

const Wrapper = styled.div`
  margin-bottom: 40px;
  display: grid;
  grid-gap: 40px 20px;
  grid-template-columns: repeat(4, calc((100% - 60px) / 4));

  @media (min-width: 768px) {
    grid-template-columns: repeat(4, calc((100% - 60px) / 4));
  }

  @media (min-width: 992px) {
    grid-template-columns: repeat(5, calc((100% - 80px) / 5));
  }

  @media (min-width: 1200px) {
    grid-template-columns: repeat(6, calc((100% - 100px) / 6));
  }

  @media (min-width: 1600px) {
    grid-template-columns: repeat(8, calc((100% - 140px) / 8));
  }

  .top {
    height: 0;
    padding-top: 140%;
    background-color: rgba(0, 0, 0, 0.1);
    background-size: cover;
    background-position: center;
    border-radius: 6px;
    vertical-align: bottom;
    position: relative;
    cursor: pointer;

    span {
      display: block;
      width: 100%;
      height: 30px;
      line-height: 30px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      text-align: center;
    }

    .update {
      position: absolute;
      right: 0;
      top: 0;
      padding: 3px 10px;
      color: white;
      background-color: rgba(0, 0, 0, 0.5);
      border-radius: 0 4px 0 4px;

      &.active {
        background-color: rgb(242, 93, 142);
      }
    }

    .rate {
      position: absolute;
      left: -5px;
      top: 5px;
      padding: 1px 15px;
      min-width: 30px;
      color: white;
      background-color: rgba(0, 0, 0, 0.7);
      border-radius: 0 2px 2px 0;
    }
  }
`;

const goAnimate = (type, slug) => {
  Router.push(`/${type}/slug/${slug}`);
};

const reactComponent = props => {
  const { list, type, update = false, status = false } = props;

  return (
    <>
      {list.length === 0 ? (
        <Empty style={{ margin: "30px 0" }} description={false} />
      ) : (
        <Wrapper>
          {list.map(item => (
            <div
              className="top"
              style={{ backgroundImage: `url(${item.coverVertical})` }}
              onClick={() => {
                goAnimate(type, item.slug);
              }}
              key={item._id}
            >
              <span>{item.title}</span>
              {update && (
                <div
                  className={
                    inThisWeek(item.updatedAt) ? "update active" : "update"
                  }
                >
                  {fixedZero(item.countEposide)}
                </div>
              )}

              <div className="rate">{item.countStar}</div>
            </div>
          ))}
        </Wrapper>
      )}
    </>
  );
};

const Wrapper2 = styled.div`
  .list {
    margin-top: 20px;
    height: 260px;
    border-radius: 5px;
    box-shadow: 0 0 16px rgba(0, 0, 0, 0.2);
    display: grid;
    grid-template-columns: 190px 1fr 190px;
    overflow: hidden;

    .img {
      background-color: rgba(0, 0, 0, 0.1);
      background-size: cover;
      border-radius: 5px;
    }

    .content {
      padding: 15px;

      .title {
        line-height: 40px;
        font-size: 26px;
        color: black;
        cursor: pointer;

        &:hover {
          color: ${props => props.color};
        }
      }

      .info {
        line-height: 25px;
        display: flex;
        justify-content: flex-start;
        align-items: center;

        span {
          display: flex;
          justify-content: flex-start;
          align-items: center;
          margin-right: 30px;

          i {
            font-size: 12px;
            opacity: 0.6;
            margin-right: 5px;
          }
          &:nth-child(2) i,
          &:nth-child(3) i,
          &:nth-child(4) i {
            font-size: 15px;
          }
        }
      }

      .tags {
        height: 25px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        font-size: 14px;
        color: rgba(0, 0, 0, 0.6);

        span {
          margin-right: 8px;
        }
      }

      .introduce {
        height: 140px;
        line-height: 20px;
        opacity: 0.8;
      }
    }

    .rate {
      background-color: rgba(0, 0, 0, 0.2);
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      padding: 15px;

      .title {
        width: 100%;
        height: 40px;
        display: flex;
        justify-content: space-between;

        span:nth-child(1) {
          font-size: 1.2em;
          color: white;
        }
      }

      .star {
        font-size: 80px;
        font-weight: solid;
        height: 160px;
        line-height: 160px;
      }
    }
  }
`;

export const ListInfo = props => {
  const { list, type } = props;
  const value = useContext(ColorContext);
  const color = useMemo(() => value, [value]);

  return (
    <>
      {list.length === 0 ? (
        <Empty style={{ margin: "15px 0 " }} />
      ) : (
        <Wrapper2 color={color}>
          {list.map(item => (
            <div className="list" key={item._id}>
              <div
                className="img"
                style={{ backgroundImage: `url(${item.coverVertical})` }}
              />
              <div className="content">
                <div
                  className="title"
                  onClick={() => {
                    goAnimate(type, item.slug);
                  }}
                >
                  {item.title}
                </div>
                <div className="info">
                  <span>
                    <IconFont type="icon-icon-time" />
                    {moment(item.information.firstPlay).format("YYYY-MM-DD")}
                  </span>
                  <span>
                    <IconFont type="icon-dianshiyouxiandianshishuzidianshimianxing" />
                    {item.information.eposideCount}
                  </span>

                  <span>
                    <IconFont type="icon-youtube" />
                    {item.count.play}
                  </span>

                  <span>
                    <IconFont type="icon-yizhuifan" />
                    {item.count.like}
                  </span>
                  <span>
                    <IconFont type="icon-comment" />
                    {item.count.comment}
                  </span>
                </div>
                <div className="tags">
                  <span>{formatMessage({ id: "list.information.tags" })}:</span>
                  {item.category.tag.map(ele => (
                    <span>{ele}</span>
                  ))}
                </div>
                <div className="introduce">
                  <p>
                    {formatMessage({ id: "list.information.introduce" })}:{" "}
                    {item.information.introduce}
                  </p>
                </div>
              </div>
              <div className="rate">
                <div className="title">
                  <span>
                    {formatMessage({ id: "list.information.rateStar" })}:
                  </span>
                  <span>
                    {formatMessage({ id: "list.information.rateCount" })}:
                    {item.information.rateCount}
                  </span>
                </div>

                <div className="star">
                  <span>{item.information.rateStar}</span>
                </div>
              </div>
            </div>
          ))}
        </Wrapper2>
      )}
    </>
  );
};

export default reactComponent;
